@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600;700;800&display=swap);
* {
    margin: 0;
    padding: 0;
}

:root {
    font-size: 62.5%;
}

body {
    height: 100vh;
    
    color: rgb(255, 255, 255);
    background: rgb(235, 242, 245);
}

/* the others don't use the default font of the body */
/* / represents line-height */
body, input, button, textarea {
    font: 400 1.8rem/1 'Nunito', sans-serif;
}

@-webkit-keyframes up {
    from {
        opacity: 0;
        transform: translateY(16px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes up {
    from {
        opacity: 0;
        transform: translateY(16px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.animate-up {
    -webkit-animation-name: up;
            animation-name: up;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
    
    /* stops on 'to'; hold the style and not back to 'from' */
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

/* the style order try to follow the same html order */

/* mobile first */
#page-landing {
    background: linear-gradient(329.54deg, #014065 0%, #002c46 100%);

    text-align: center;
    min-height: 100vh;
    /* min-width: 439px; */

    display: flex;
}

#container {
    margin: auto;

    width: min(90%, 112rem);
}

.location, h1, section p {
    height: 16vh
}

.logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding-bottom: 60px;

    -webkit-animation-delay: 50ms;

            animation-delay: 50ms;
}

.logo p {
    padding-left: 10px;
    font-size: 24px;
    font-weight: 800;
}

.location {
    -webkit-animation-delay: 100ms;
            animation-delay: 100ms;
}

.main h1 {
    -webkit-animation-delay: 150ms;
            animation-delay: 150ms;

    /* clamp(min, ideal, max) */
    font-size: clamp(4rem, 8vw, 8.4rem);
}

section p {
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms;

    padding-top: 140px;
}

.visit {
    width: 8rem;
    height: 8rem;
    background: rgb(247, 150, 51);

    border: none;
    border-radius: 3rem;

    display: flex;
    align-items: center;
    justify-content: center;

    margin: auto;

    transition: background .2s;

    -webkit-animation-delay: 250ms;

            animation-delay: 250ms;
}

.visit:hover {
    background: rgb(110, 193, 228); 
}

/* desktop */
@media(min-width:760px) {
    #container {
        background: url(/tere-agasalhado-web/static/media/landing.db34ba2f.png) 
            no-repeat 80% / clamp(40rem, 54vw, 76rem);
    }

    header {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .location, h1, section p {
        height: auto;
        height: initial;
        text-align: left;
        text-align: initial;
    }

    .logo {
        padding-bottom: 0;
    }

    .location {
        text-align: right;
        font-size: 2.4rem;
        line-height: 1.5;
    }

    main h1 {
        font-weight: bold;
        line-height: .88;

        margin: clamp(10%, 9vh, 12%) 0 4rem;

        width: min(300px, 80%);
    }

    section {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    section p {
        font-size: 2.4rem;
        line-height: 1.5;
        width: clamp(20rem, 20vw, 30rem);

        padding-top: 0;
    }

    .visit {
        margin: 0;
        margin: initial;
    }
}

#page-map {
    width: 100vw;
    height: 100vh;

    display: flex;
    flex-direction: column;

    position: relative;
}

#page-map aside {
    padding: 10px;
    background: linear-gradient(329.54deg, #014065 0%, #002c46 100%);

    display: flex;
    flex-direction: column;
}

#page-map aside header {
    align-items: center;
    
    display: flex;
    flex-direction: column;
}

#page-map aside h2 {
    font-size: 35px;
    font-weight: 700;
    line-height: 32px;
    margin-top: 25px;
    text-align: center;
}

#page-map aside p {
    line-height: 28px;
    margin-top: 24px;
    text-align: center;
}

#page-map aside footer {
    line-height: 24px;
    margin-top: 24px;
    text-align: center;

    display: flex;
    flex-direction: column;
}

#page-map aside footer strong {
    font-weight: 800;
}

#page-map .leaflet-container {
    z-index: 5;
}

#page-map .create-collect-point {
    position: absolute;
    right: 40px;
    bottom: 40px;

    z-index: 10;

    width: 64px;
    height: 64px;
    background: rgb(247, 150, 51);
    border-radius: 20px;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: background-color 0.2s;
}

#page-map .create-collect-point:hover {
    background: rgb(110, 193, 228); 
}

/* desktop */
@media(min-width:760px) {
    #page-map {
        flex-direction: row;
    }
    
    #page-map aside {
        padding: 70px;
        width: 30%;

        align-items: flex-start;
        justify-content: space-between;
    }
    
    #page-map aside header {
        align-items: flex-start;
        
    }
    
    #page-map aside h2 {
        margin-top: 70px;
        text-align: start;
    }
    
    #page-map aside p {
        margin-top: 70px;
        text-align: start;
    }
    
    #page-map aside footer {
        margin-top: 0;
    }
}

